.App {
    color: black;
    text-align: center;
}

.title.app {
    font-size: 50px;
}

.desc {
    color: dimgray;
}

.desc.large {
    color: dimgray;
    font-size: 25px;
}

.anchor {
    background-color: black;
    border-radius: 15px;
    padding: 15px;
    color: white;
    text-decoration: none;
    transition: 0.3s ease-out;
}

.anchor:hover {
    color: lightblue;
    padding: 18px;
}